<template>
    <div class="ui-kit-select" :class="{visible: visible}">
        <div class="ui-kit-select-title" @click="visible = !visible" :class="{active: visible}">
            <label :class="{placeholder: !value}">{{cpd_label}}</label>
            <div class="right-icon"><van-icon name="play" /></div>
        </div>
        <div class="ui-kit-select-content">
            <div class="ui-kit-select-options">
                <div class="ui-kit-select-item" v-for="(item, index) in options" :key="index" @click="setValue(item)">
                    {{formatItemLabel(item)}}
                </div>
            </div>
            <div class="ui-kit-select-overlay" @click="visible = false"></div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'UiKitSelect',
    model: {
        prop: 'value',
        event: 'change'
    },
    props: {
        value: {
            type: String | Number,
            default: ''
        },
        placeholder: {
          type: String,
          default: '请选择'
        },
        options: {
            type: Array,
            default: () => []
        },
        fieldLabel: {
            type: String,
            default: 'label'
        },
        fieldValue: {
            type: String,
            default: 'value'
        }
    },
    data() {
        return {
            visible: false
        }
    },
    computed: {
        cpd_label() {
            const values = this.options.filter(item => item[this.fieldValue] === this.value)
            const value = values.length > 0 ? values[0][this.fieldLabel] : ''
            return value || this.placeholder
        }
    },
    methods: {
        formatItemLabel(item) {
            return item[this.fieldLabel]
        },
        setValue(item) {
            this.$emit('change', item[this.fieldValue])
            this.visible = false
        }
    }
}
</script>

<style lang="less">
.ui-kit-select {
    position: relative;
    height: 28px;
    z-index: 1;
    .ui-kit-select-title {
        display: flex;
        justify-content: space-between;
        border: solid 1px #555;
        padding: 0 12px;
        align-items: center;
        height: 28px;
        background-color: #1c1c1c;
        cursor: pointer;
        border-radius: 14px;
        &.active {
            border-radius: 14px 14px 0 0;
        }
        label {
            padding-right: 12px;
            cursor: pointer;
        }
        .right-icon {
            transform: rotate(90deg);
            font-size: 12px;

        }
    }
    .ui-kit-select-content {
        position: absolute;
        width: 100%;
        left: 0;
        background-color: #1c1c1c;
        border: solid 1px #555;
        display: none;
        padding: 6px 0;
        .ui-kit-select-item {
            padding: 6px 12px;
            cursor: pointer;
        }
        border-radius: 0 0 14px 14px;
    }
    &.visible {
        .ui-kit-select-title {

        }
        .ui-kit-select-content {
            border-top: none;
            display: block;
            .ui-kit-select-options {
                position: relative;
                z-index: 3999;
            }
            .ui-kit-select-overlay {
                position: fixed;
                width: 100vw;
                height: 100vh;
                z-index: 3998;
                top: 0;
                left: 0;
            }
        }
    }
}
</style>
